import { supabase } from '~/supabase';
import type { Employee, Location, Req } from '~/types';
import { useAuthStore } from '#imports';

export const useRequestStore = defineStore('requestStore', () => {
    const { txt } = storeToRefs(useAppStore());
    const { active_emp_id, isManager, profile } = storeToRefs(useAuthStore());
    const _isPending = ref<boolean>(false);
    const isReqPending = ref<boolean>(false);
    const requests = ref<Array<Req>>([]);
    const requests_cnt = ref<number>(0);
    const statuses = computed( () => [
        { id: 1, name: txt.value.pending },
        { id: 2, name: txt.value.approved },
        { id: 3, name: txt.value.denied }
    ]);

    const isPending = computed(() => { return _isPending.value });
    
    const params = ref<any>({
        isAsc: true,
        orderBy: 'created_at',
        page: 1,
        perPage: 10,
        search: ''
    });

    const startIndex = computed(() => { return (params.value.page - 1) * params.value.perPage; });
    const endIndex = computed(() => { return startIndex.value + params.value.perPage - 1 });

    const blankRequest = computed((): Req => {
        let tomorrow = new Date();
        tomorrow.setDate(tomorrow.getDate() + 1);
        tomorrow.setHours(0, 0, 0, 0);
        const start = new Date(tomorrow);
        const end = new Date(tomorrow);
        start.setHours(0, 0, 0, 0);
        end.setHours(23, 45, 0, 0)
        return {
            id: 0,
            created_by: profile.value.account_id,
            created_at: new Date(),
            modified_at: new Date(),
            employee: {} as Employee,
            location: {} as Location,
            status: 1,
            start: start,
            end: end,
            note: ''
        };
    });

    function $reset(): void {
        _isPending.value = false;
        requests.value = [];
        requests_cnt.value = 0;
        params.value = {
            isAsc: true,
            orderBy: 'created_at',
            page: 1,
            perPage: 10,
            search: ''
        };
    }

    async function fetchRequests(location_id: number): Promise<string> {
        if (_isPending.value) return 'Error:  Please try again';
        _isPending.value = true;

        let msg = '';

        if (isManager.value) {

            const { count: pendingReqCnt } = await supabase
                .from('request')
                .select(undefined, { count: 'exact', head: false })
                .eq('location_id', location_id)
                .eq('employee_id', active_emp_id.value)
                .eq('status', 1);
            
            if (pendingReqCnt) { isReqPending.value = pendingReqCnt > 0 }
            else { isReqPending.value = false; }
        }

        let query = supabase
            .from('request')
            .select('id, created_by, created_at, modified_at, employee:employee_id(*), location:location_id(*), status, start, end, note', { count: 'exact', head: false })
            .eq('location_id', location_id);

        if (!isManager.value) {
            query = query.eq('employee_id', active_emp_id.value);
        }

        query = query.order('created_at', { ascending: true })
                     .range(startIndex.value, endIndex.value);
        
        const { data, count, error } = await query;
        
        if (error) {
            msg = error.message as string;
        }
        else if (data) {
            requests.value = (data as Array<Req>).map(d => {
                return {
                    ...d,
                    start: new Date(d.start),
                    end: new Date(d.end) ,
                }
            });
            requests_cnt.value = count || 0;
        }

        _isPending.value = false;
        return msg;
    }

    async function fetchRequestsForSchedule(location_id: number, startDate: Date, endDate: Date): Promise<string> {
        if (_isPending.value || !profile.value) return 'Error:  Please try again';
        _isPending.value = true;

        let msg = '';

        const start = new Date(startDate);
        const end = new Date(endDate);

        const { data, count, error } = await supabase
            .from('request')
            .select('id, created_by, created_at, modified_at, employee:employee_id(*), location:location_id(*), status, start, end, note', { count: 'exact', head: false })
            .eq('location_id', location_id)
            .neq('status', 3)
            .lt('start', end.toISOString())
            .gt('end', start.toISOString())
            .order('employee(fname)', { ascending: true })
            .order('start', { ascending: true });
        
        if (error) {
            msg = error.message as string;
        }
        else if (data) {
            requests.value = (data as Array<Req>).map(d => {
                return {
                    ...d,
                    start: new Date(d.start),
                    end: new Date(d.end) ,
                }
            });
            requests_cnt.value = count || 0;
        }

        _isPending.value = false;
        return msg;
    }

    async function addUpdateRequest(request: Req): Promise<string> {
        if (_isPending.value) return 'Error:  Please try again';
        _isPending.value = true;

        let msg = '';
        let query = null;

        if (!request.id || request.id === 0) {
            query = supabase
                .from('request')
                .insert([
                    {
                        created_by: profile.value.account_id,
                        created_at: new Date().toISOString(),
                        employee_id: request.employee.id,
                        location_id: request.location.id,
                        start: new Date(request.start).toISOString(),
                        end: new Date(request.end).toISOString(),
                        note: request.note
                    }
                ])
                .select();
        }
        else {
            query = supabase
                .from('request')
                .update([
                    {
                        modified_at: new Date().toISOString(),
                        employee_id: request.employee.id,
                        location_id: request.location.id,
                        status: request.status,
                        start: new Date(request.start).toISOString(),
                        end: new Date(request.end).toISOString(),
                        note: request.note
                    }
                ])
                .eq('id', request.id)
                .select();
        }

        const { error } = await query;

        if (error) {
            msg = error.message as string;
        }

        _isPending.value = false;
        return msg;
    }

    /**
     * Set the status of a request.
     * 
     * @param {Req} request 
     * @param {number} status The status code [pending: 1, approved: 2, denied: 3]
     */
    async function setStatus(request: Req, status: number): Promise<string> {
        if (_isPending.value) return 'Error:  Please try again';

        status = Math.floor(status);
        if (status < 1 || status > 3) return 'Invalid status.';

        request.status = status;

        return await addUpdateRequest(request);
    }

    async function deleteRequest(id: number) {
        if (_isPending.value) return 'Error:  Please try again';
        _isPending.value = true;

        let msg = '';

        const { error } = await supabase
            .from('request')
            .delete()
            .eq('id', id);

        if (error) {
            msg = error.message as string;
        }

        _isPending.value = false;
        return msg;
    }

    return {
        blankRequest,
        requests,
        requests_cnt,
        isPending,
        isReqPending,
        params,
        statuses,
        addUpdateRequest,
        deleteRequest,
        fetchRequests,
        fetchRequestsForSchedule,
        setStatus,
        $reset
    }
});

if (import.meta.hot) {
    import.meta.hot.accept(acceptHMRUpdate(useRequestStore, import.meta.hot))
}