import { ref, computed } from "vue";
import { defineStore } from "pinia";
import { useAppStore, useAuthStore, useRequestStore } from "#imports";

export const usePageStore = defineStore('pageStore', () => {
    const { txt } = storeToRefs(useAppStore());
    const authStore = useAuthStore();
    const { currentLocationId, isManager, isMember, redeemedCodes } = storeToRefs(authStore);
    const requestStore = useRequestStore();
    const { isReqPending } = storeToRefs(requestStore);
    const { fetchRequests } = requestStore;

    fetchRequests(currentLocationId.value);

    const _isPending = ref<boolean>(false);
    const changesExist = ref<boolean>(false);
    const isForceNav = ref<boolean>(false);
    const isWarningShown = ref<boolean>(false);
    const tempNavTo = ref<Object | null>(null);
    const prevRoute = ref<string>('');
    const pages = computed(() => [
        {
            title: txt.value.account,
            id: 1,
            icon: 'mdi-account-circle-outline',
            to: '/account',
            isShow: true
        },
        {
           title: txt.value.schedules,
           id: 2,
           icon: 'mdi-calendar-blank-multiple',
           to: '/schedules',
           isShow: isMember.value && redeemedCodes.value.length > 0
        },
        {
           title: txt.value.setup,
           id: 3,
           icon: 'mdi-card-account-details-outline',
           to: '/setup',
           isShow: isManager.value && redeemedCodes.value.length > 0
        },
        {
           title: isReqPending.value ? `${txt.value.requests} â—` : txt.value.requests,
           id: 4,
           icon: 'mdi-email-multiple-outline',
           to: '/request',
           isShow: isMember.value && redeemedCodes.value.length > 0
        },
        {
           title: txt.value.support,
           id: 5,
           icon: 'mdi-frequently-asked-questions',
           to: '/support',
           isShow: isMember.value
        }
    ]);

    const isPending = computed(() => { return _isPending.value });

    function $reset(): void {
        changesExist.value = false;
        isForceNav.value = false;
        isWarningShown.value = false;
        tempNavTo.value = null;
        _isPending.value = false;
    }

    return {
        pages, changesExist, isForceNav, isWarningShown, tempNavTo, isPending, prevRoute,
        $reset
    }
});

if (import.meta.hot) {
    import.meta.hot.accept(acceptHMRUpdate(usePageStore, import.meta.hot))
}